import { useSetRecoilState } from "recoil";
import styled from "styled-components";
import { useForm } from "react-hook-form";
import { ITimeLog, TimeLogState } from "../atoms";
import { motion } from "framer-motion";
import { useState } from "react";

interface IDisplay {
    displayele: string;
}

const TimeLogEle = styled.li`
    font-size: 14px;
    margin-bottom: 5px;
    text-align: center;
    padding-left: 30px;
    &:last-chlid {
        margin-bottom: 0;
    }
`;
const FormContainer = styled(motion.span)<IDisplay>`
    display: ${(props) => props.displayele};
`;
const InputToDo = styled.input`
    background-color: transparent;
    border: none;
    border-bottom: solid 1px transparent;
    border-radius: 0;
    transition: border 0.4s ease;
    color: #fff;
    width: 130px;
    &:focus {
        border-bottom: solid 1px #fff;
        outline: none;
    }
`;
const DeleteBtn = styled.button`
    border: none;
    background-color: transparent;
    color: #ed3b3b;
    font-size: 14px;
    width: 30px;
    height: 30px;
    cursor: pointer;
`;

interface ITime {
    timeTrack: string;
}
function TimeLog({ id, tableId, time } : ITimeLog) {
    const [displayForm, setDisplayForm] = useState(false);
    
    const setTimeLogs = useSetRecoilState(TimeLogState);
    const {register, handleSubmit, setValue} = useForm<ITime>();
    const deleteTime = () => {
        setTimeLogs((prevTimeLogs) => {
            const newTimeLogs = [...prevTimeLogs].filter((log) => log.id !== id);
            return newTimeLogs;
        })
    };
    const handleEditForm = () => {
        setDisplayForm((prev) => !prev);

    }
    const onValid = ({timeTrack} : ITime) => {
        setTimeLogs((prevTimeTrack) => {
            const timeTrackIndex = prevTimeTrack.findIndex((timetrack) => timetrack.id === id);
            const newTimeTrack = {
                id,
                tableId,
                time: timeTrack,
            };
            const newTimeTracks = [...prevTimeTrack.slice(0, timeTrackIndex), newTimeTrack, ...prevTimeTrack.slice(timeTrackIndex + 1)];
            return newTimeTracks;
        })
        setDisplayForm((prev) => !prev);
        setValue("timeTrack", "");
    }
    return (<TimeLogEle id={tableId}>
        <FormContainer 
            displayele={displayForm? "none" : "inline-block"} 
            onClick={handleEditForm}>
            {time}
        </FormContainer>
        <FormContainer displayele={displayForm? "inline-block" : "none"}>
            <form onSubmit={handleSubmit(onValid)}>
                <InputToDo {...register("timeTrack", {
                    required: "Please write Time",
                    value: time,
                    })} placeholder="00:00:00" />
            </form>
        </FormContainer>
        <DeleteBtn onClick={deleteTime}>&times;</DeleteBtn>
    </TimeLogEle>);
}

export default TimeLog;