import { useRecoilValue, useSetRecoilState } from "recoil";
import styled from "styled-components";
import { motion } from "framer-motion";
import { ITimeTable, TimeLogState, TimeTableState, TIMETRACK_HEIGHT } from "../atoms";
import TimeLog from "./TimeLog";

const TimeList = styled(motion.div)`
    width: 250px;
    display: inline-block;
    background-color: rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(8px);
    padding: 10px;
    border-radius: 15px;
    margin: 10px;
    color: #333;
    font-weight: 600;
    overflow-y: auto;
`;

const TimeListTitle = styled.div`
    padding: 10px 15px 10px 40px;
    border-radius: 15px;
    font-size: 19px;
    color: ${props => props.theme.accentColor};
    background-color: rgba(255, 255, 255, 1);
    text-align: center;
    margin-bottom: 10px;
`;

const IsTargetCheck = styled.input`
    height: 17px;
    width: 17px;
    top: 4px;
    margin-left: 10px;
`;

const timeTableVariants = {
    initial: {
        height: `${TIMETRACK_HEIGHT/2}vh`,
        opacity: 0,
    },
    animate: {
        height: `${TIMETRACK_HEIGHT}vh`,
        opacity: 1,
        transition: {
            duration: 0.3,
        }
    }
};

function TimeTable( { id, date, isTarget } : ITimeTable) {
    const timeLogs = useRecoilValue(TimeLogState);
    const setTimeTable = useSetRecoilState(TimeTableState);
    const onChangeIsTarget = () => {
        setTimeTable((prevTables) => {
            const tableIndex = prevTables.findIndex((table) => table.id === id);
            const newTable = {
                id,
                date,
                isTarget: !isTarget,
            };
            const newTables = [...prevTables.slice(0, tableIndex - 1), newTable, ...prevTables.slice(tableIndex + 1)];
            return newTables;
        });
    }
    return (<TimeList 
        variants={timeTableVariants}
        initial="initial"
        animate="animate"
    >
        <div>
            <TimeListTitle>
                {date}
                <IsTargetCheck 
                    checked={isTarget} 
                    onChange={onChangeIsTarget}
                    type="checkbox"
                />
            </TimeListTitle>
        </div>
        <ul>
            {timeLogs?.map((timeLog, index) => (
                <TimeLog key={index} {...timeLog}/>
            ))}
        </ul>
    </TimeList>);
}

export default TimeTable;