import { useEffect, useState } from 'react';
import { useRecoilState, useSetRecoilState } from 'recoil';
import styled from 'styled-components';
import { TimeLogState, TimeTableState, TIMETRACK_HEIGHT } from './atoms';
import TimeTable from './components/TimeTable';

const Container = styled.div`
    width: 100vw;
    height: 100vh;
    position: relative;
`;

const BackgroundContainer = styled.div`
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
`;

const ListContainer = styled.ul`
    height: calc(${TIMETRACK_HEIGHT}vh + 25px);
    overflow-x: auto;
    white-space: nowrap;
`;
const Button = styled.button`
    position: absolute;
    bottom: 8%;
    left: 50%;
    transform: translateX(-50%);
    width: 120px;
    height: 45px;
    font-size: 15px;
    border-radius: 25px;
    border: none;
    color: ${(props) => props.theme.bgColor};
    background-color: ${(props) => props.theme.textColor};
    box-shadow: ${(props) => props.theme.shadowColor};
    transition: background-color 0.3s ease;
    cursor: pointer;
    &:hover {
        background-color: rgb(207, 224, 247);
    }
`;


function TimeTracker() {
    const [isTodayTrack, setIsTodayTrack] = useState(false);
    const [timeTables, setTimeTables] = useRecoilState(TimeTableState);
    const setTimeLogs = useSetRecoilState(TimeLogState);
    
    const [thisTime, setThisTime] = useState('');
    const [thisDay, setThisDay] = useState('');

    const settingDayTime = () => {
        const day = getToday();
        const time = getTime();
        setThisTime(time);
        setThisDay(day);
    }
    useEffect(settingDayTime, []);

    const newTable = {
        id: `i${Date.now()}`,
        date: thisDay,
        isTarget: false,
    };
    const newTime = {
        id: `t${Date.now()}`,
        tableId: newTable.id,
        time: thisTime,
    }
    
    const getToday = () => {
        const date = new Date();
        const year = date.getFullYear();
        const month = date.getMonth() + 1;
        const day = date.getDate();
        const today = `${year}-${month}-${day}`;
        return today;
    }

    const getTime = () => {
        const date = new Date();
        const hours = String(date.getHours()).padStart(2, "0");
        const minutes = String(date.getMinutes()).padStart(2, "0");
        const seconds = String(date.getSeconds()).padStart(2, "0");
        const time = `${hours}:${minutes}:${seconds}`;
        return time;
    }

    const printTimeTrack = () => {
        if(getToday() === thisDay) {
            setIsTodayTrack(true);
        } else {
            setIsTodayTrack(false);
        }
        if(!isTodayTrack) {
            settingNewTable();
        }
        logTimeTrack();
    }
    
    const settingNewTable = () => {
        const today = getToday();
        setThisDay(today);
        setTimeTables((prevTimeTable) => {
            const newTimeTable = [...prevTimeTable, newTable];
            return newTimeTable;
        });
    }

    const logTimeTrack = () => {
        const time = getTime();
        setThisTime(time);
        setTimeLogs((prevTimeLog) => {
            const newTimeLog = [...prevTimeLog, newTime];
            return newTimeLog;
        })
    }
    return (<Container>
        <BackgroundContainer/>
        <ListContainer>
            {timeTables?.map((timeTable) => 
                <TimeTable key={timeTable.id} {...timeTable} />
            )}
        </ListContainer>
        <Button onClick={printTimeTrack}>Time Track</Button>
    </Container>);
}

export default TimeTracker;